cas y contextos de exploración que invitan al estudiante a aplicar sus propias estrategias cognitivas. Según Ausubel (1968), el aprendizaje significativo ocurre cuando el estudiante es capaz de relacionar la nueva información con sus conocimientos previos, reorganizándolos y construyendo nuevos significados. En este sentido, el docente debe fomentar la reflexión y el análisis crítico, permitiendo que el estudiante realice conexiones profundas y establezca un puente entre lo que ya sabe y lo que está aprendiendo.")])])])]),_c('div',{staticClass:"tabs-content border px-4 p-md-5 py-3",attrs:{"titulo":"Como facilitador del aprendizaje","iconUrl":require('@/assets/curso/unidad/img-43.svg')}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"d-none d-lg-block col-md-12 col-lg-5 col-xl-4",attrs:{"data-aos":"fade-right"}},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/unidad/img-48.png"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-md-12 col-lg-7 col-xl-8 mb-4 mb-md-0",attrs:{"data-aos":"fade-left"}},[_c('h4',[_vm._v("Como facilitador del aprendizaje")]),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade-up"}},[_vm._v("Actúa como un facilitador que proporciona andamiajes o apoyos temporales para que el estudiante alcance un nivel de comprensión más avanzado. Esta idea, inspirada en la teoría de la \"zona de desarrollo próximo\" de Vygotsky (1978), sugiere que el docente ofrece una guía inicial que ayuda al estudiante a enfrentar un contenido complejo, retirando progresivamente el apoyo a medida que el estudiante se vuelve más competente. ")]),_c('p',{staticClass:"mb-0"},[_vm._v("De esta manera, el aprendizaje es un proceso activo y guiado en el que el docente interviene, de manera estratégica, ajustando el nivel de apoyo de acuerdo con las necesidades y avances de cada estudiante.")])])])])]),_vm._m(7),_vm._m(8),_vm._m(9),_c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-12 col-xl-7 d-flex flex-column"},[_c('p',{static