 psicólogos como John B. Watson y B.F. Skinner, el aprendizaje se entiende como un cambio en el comportamiento observable del estudiante, que es influenciado mediante estímulos y refuerzos externos (Skinner, 1953). ")]),_c('div',{staticClass:"d-flex align-items-start bg-rosa px-4 py-5 mb-4",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"pe-xl-3 mb-0",attrs:{"data-aos":"fade-up"}},[_vm._v("Así, el docente asume un rol estratégico en el diseño y la implementación de métodos de enseñanza que promuevan conductas deseables, empleando herramientas como el refuerzo positivo y negativo para incentivar o reducir ciertas respuestas en el estudiante.")])]),_c('p',{staticClass:"mb-4"},[_vm._v("Ampliemos la información sobre el rol del docente en el conductismo:")])]),_c('div',{staticClass:"d-none d-xl-block col-xl-4 mx-auto mt-4 mt-xl-0",attrs:{"data-aos":"fade-right"}},[_c('img',{attrs:{"src":require("@/assets/curso/unidad/img-37.png"),"alt":"Texto que describa la imagen"}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-12 col-md-11 col-lg-10 mx-auto"},[_c('div',{staticClass:"d-flex align-items-center"},[_c('div',{staticClass:"d-flex align-items-center bg-amarillo px-3 py-4"},[_c('img',{staticClass:"me-4",staticStyle:{"width":"90px"},attrs:{"src":require("@/assets/curso/unidad/img-41.svg"),"alt":"Texto que describa la imagen"}}),_c('div',{staticClass:"d-flex flex-column"},[_c('p',{staticClass:"text-bold mb-1"},[_vm._v("Reflexione:")]),_c('p',{staticClass:"pe-xl-5 mb-0"},[_vm._v("¿Cómo puede un docente, dentro del marco del conductismo, equilibrar su función como guía y facilitador del aprendizaje, considerando que esta teoría se centra en el comportamiento observable y en el uso de refuerzos, sin limitar la capacidad de los estudiantes para desarrollar un aprendizaje significativo y autónomo?")])])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_v